<template>
  <blur :blurSrc="blurSrc" :blurValue="30">
    <div class="rank">
      <h2>排行榜</h2>
      <table class="rank-container">
        <thead>
        <tr>
          <th width="16%">名次</th>
          <th>昵称</th>
          <th>分数</th>
          <th>时间</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in ranks" :key="item.index" :class="{active:item.id===player.id}">
          <td><span>{{ put0(item.place) }}</span></td>
          <td><span>{{item.name}}</span></td>
          <td><span>{{item.score}}</span></td>
          <td><span>{{item.time}}</span></td>
        </tr>
        <tr v-if="rank.length" class="active">
          <td width="16%"><span>{{put0(rank[0].place)}}</span></td>
          <td><span>{{rank[0].name}}</span></td>
          <td><span>{{rank[0].score}}</span></td>
          <td><span>{{rank[0].time}}</span></td>
        </tr>
        </tbody>
      </table>
    </div>
  </blur>
</template>

<script>
  import Blur from '../../blur/blur';
  import {rank} from '../../../api/user';

  export default {
    name: "rank",
    props: {
      blurSrc: {
        type: String
      },
      score: 0,
      time: 0,
    },
    data() {
      return {
        ranks: [],
        rank: [],
        player: null,
      }
    },
    components: {
      Blur
    },
    mounted() {
      let user = JSON.parse(localStorage.getItem('nihongo'));
      rank(user.id, this.score, this.time).then((res) => {
        this.ranks = res.data.data;
        if (!res.data.mate.player.flag) {
          this.rank = res.data.mate.player.rank;
        }
        this.player = res.data.mate.player.rank[0];
        console.log(this.ranks,this.player);
      });
      this.$emit('gameInit');
    },
    computed: {
      put0() {
        return (num) => {
          return num < 10 ? '0' + num : num;
        }
      }
    }
  }
</script>

<style scoped>
  .rank {
    display: flex;
    flex-direction: column;
    padding: 20px 10px;
  }

  h2 {
    text-align: center;
    color: #333;
  }

  .rank-container {
    color: #333;
  }

  .rank-container th {
    font-weight: initial;
    padding-bottom: 3px;
  }

  .rank-container > thead > tr > th:nth-of-type(n+2) {
    text-align: left;
    text-indent: 2ch;
  }

  .rank-container > tbody > tr > td {
    padding: 5px 0 3px;
  }

  .rank-container > tbody > tr > td:nth-of-type(1) {
    text-align: center;
  }

  .rank-container > tbody > tr > td:nth-of-type(n+2) {
    text-indent: 2ch;
  }

  .rank-container {
    border-collapse: collapse;
  }

  .rank-container > thead > tr {
    border-bottom: 1px solid rgba(255, 255, 255, .8);
  }

  .rank-container > tbody > tr.active {
    background: rgba(255, 255, 255, .3);
  }

</style>